.container {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;

  .block {
    position: absolute;
    width: 120px;
    height: 69px;

    .item {
      width: 100%;
      height: 100%;
      position: relative;

      .h1 {
        left: 0;
        animation: load1 7.2s ease infinite;
      }
      .h2 {
        right: 0;
        animation: load2 7.2s ease 0.6s infinite;
      }
      .h3 {
        right: 0;
        animation: load3 7.2s ease 1.2s infinite;
      }
      .h4 {
        right: 0;
        animation: load4 7.2s ease 1.8s infinite;
      }
      .h5 {
        left: 0;
        animation: load5 7.2s ease 2.4s infinite;
      }
      .h6 {
        left: 0;
        animation: load6 7.2s ease 3s infinite;
      }
    }

    .item span {
      position: absolute;
      width: 4px;
      height: 0%;
      background: #053146;
      z-index: 999999;
    }
  }

  .block:nth-of-type(2) {
    transform: rotate(60deg);
  }
  .block:nth-of-type(3) {
    transform: rotate(-60deg);
  }
}

@keyframes load1 {
  0% {
    bottom: 0;
    height: 0;
  }
  6.944444444% {
    bottom: 0;
    height: 100%;
  }
  50% {
    top: 0;
    height: 100%;
  }
  59.944444433% {
    top: 0;
    height: 0;
  }
}

